.header__email {
    margin: 0;
    margin-right: 24px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
    color: #ffffff;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 770px) {
    .header__email {
        margin-right: 0;
        margin-bottom: 18px;
    }
}

@media screen and (max-width: 640px) {
    .header__email {
        max-width: 189px;
    }
}